import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-allowance-deduction-detail',
  templateUrl: './allowance-deduction-detail.component.html',
  styleUrls: ['./allowance-deduction-detail.component.css']
})
export class AllowanceDeductionDetailComponent implements OnInit {
  isLoading: boolean = false;
  selectedType: number;
  selectedDeductionType: number;
  selectedGovernmentType: number;
  isType: boolean = true;
  isGov: boolean = true;
  moduleType = [
    { id: 0, description: 'Addition' },
    { id: 1, description: 'Deduction' },
  ];
  deductionType = [
    { id: 0, description: 'Government' },
    { id: 1, description: 'Regular' },
  ];
  governmentType = [
    { id: 0, description: 'SSS' },
    { id: 1, description: 'Pagibig' },
    { id: 2, description: 'Philhealth' },
  ];
  constructor() { }

  ngOnInit() {
    this.isLoading = false;
  }

  modelChangeFn(e){
    if(e.id == 0){
      this.isType = true;
      this.isGov = true;
    }
    else{
      this.isType = false;
      this.isGov = false;
      if(this.selectedDeductionType === 0){
        this.isGov = false;
      }
      else{
        this.isGov = true;
      }
    }
  }

  modelDeductionFn(e){
    if(e.id === 0){
      this.isGov = false;
    }
    else{
      this.isGov = true;
    }
  }

}
