import { Router } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { BranchService } from '../../../../services/administration/branch/branch.service';

@Component({
  selector: 'app-branch-view',
  templateUrl: './branch-view.component.html',
  styleUrls: ['./branch-view.component.css']
})
export class BranchViewComponent implements OnDestroy, OnInit {

  dtOptions: DataTables.Settings = {};
  branchList = [];

  dtTrigger: Subject<any> = new Subject<any>();
  constructor(private branchService: BranchService, private router: Router) {
   }

  ngOnInit() {
     this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 5,
      processing: true
    };
    this.branchService.branchList("0").subscribe(data => { 
      this.branchList = data;
      this.dtTrigger.next();
    })    
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

  viewBranch(id){
    this.router.navigate(["/layout/administration/branch-detail", id]);
  }

}
